/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"container w-80\">\r\n\r\n\t<p>Hello, this is a pet (demonstrative) site of <a href=\"https://gralievskyi-cv.herokuapp.com/\">Gralevskyi Oleksandr.</a></p>\r\n\t<p>As for me the fastest way to learn foreign lexicon is learning the most frequently used words in sphere you are interested in. \r\n\t<p>\r\n\t\tAnd this site can help you in such approach. You can take any text you are interested in and parse it into words. \r\n\t\tIn the result of parsing you receive a number of times a word is occured in the text. \r\n\t\tYou can save your result of parsing and after that delete unnecessary words. \r\n\t\tIn such way you can form lists of words for learning.\r\n\t</p>\r\n\t<p>The back end (server) application was created using Java 8 and Spring framework. \r\n\tIt uses JWT Token for security purpose and MySQL as data base. Server API is deployed on AWS Elasticbeanstalk. The code is on <a href=\"https://github.com/Gralevskyi/TextParser\">git hub.</a></p>\r\n\t<p>The front end is running using Angular JS. The code is also located on <a href=\"https://github.com/Gralevskyi/TextParserFront\">git hub.</a></p>\r\n\t<p>I'm looking for a job of Java developer and you can find my <a href=\"https://gralievskyi-cv.herokuapp.com/\">portfolio here</a> or look through my accont on \r\n\t\t\t<a href=\"https://www.linkedin.com/in/oleksandr-gralievskyi-46301517a/?originalSubdomain=ua\">linkedin.</a></p>\r\n\r\n</div>");

